<template>
  <div>
    <div class="bg-primary py-4">
      <h1 class="text-white text-center display-4"><i class="fab fa-connectdevelop"></i></h1>
    </div>
    <div class="container my-5">
      <h2 class="text-center text-primary">聯絡資訊</h2>
      <div class="form-row pt-3">
        <div class="input-group col-2">
          <input type="text" class="form-control" v-model="contact.name" />
        </div>
        <div class="input-group col-3">
          <input type="text" class="form-control" v-model="contact.phone" />
        </div>
        <div class="input-group col-6">
          <input type="text" class="form-control" v-model="contact.address" />
        </div>
        <button class="btn btn-primary col-1" @click.prevent="addContact">新增</button>
      </div>
      <div class="form-row py-3">
        <div class="input-group col-2">
          <select class="form-control" name id v-model="filter">
            <option value="name">姓名</option>
            <option value="phone">聯絡方式</option>
            <option value="address">地址</option>
          </select>
        </div>
        <div class="input-group col-10">
          <input type="text" class="form-control" v-model="search" />
          <div class="input-group-append">
            <button class="btn btn-primary"><i class="fas fa-search"></i></button>
          </div>
        </div>
      </div>
      <table class="table table-hover text-dark mb-5">
        <thead class="bg-secondary">
          <tr>
            <th width="120px">姓名</th>
            <th width="180px">聯絡方式</th>
            <th>地址</th>
            <th width="120px">編輯</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(item, key) in searchData" :key="key" @dblclick="getedit(item)">
            <td>
              <span v-if="editContact != item">{{ item.name }}</span>
              <input type="text" class="form-control" v-if="editContact == item" v-model="editName" @keyup.esc="escedit" @keyup.enter="doneedit(item)">
            </td>
            <td>
              <span v-if="editContact != item">{{ item.phone }}</span>
              <input type="text" class="form-control" v-if="editContact == item" v-model="editPhone" @keyup.esc="escedit" @keyup.enter="doneedit(item)">
            </td>
            <td>
              <span v-if="editContact != item">{{ item.address }}</span>
              <input type="text" class="form-control" v-if="editContact == item" v-model="editAddress" @keyup.esc="escedit" @keyup.enter="doneedit(item)">
            </td>
            <td>
              <div class="btn-group">
                <button class="btn btn-sm btn-primary" @click.prevent="editbtn(item)">編輯</button>
                <button class="btn btn-sm btn-danger" @click.prevent="delContacts(item.Id)">刪除</button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contactData: [],
      contact: {
        name:'',
        phone:'',
        address: '',
      },
      editContact:{},
      editName:'',
      editPhone:'',
      editAddress:'',
      filter: "name",
      search: "",
      docId: '',
      edit: false,
    };
  },
  methods: {
    signin() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}admin/signin`;
      const user = {
        username: "zm9poiuy@gmail.com",
        password: "romeo14124869"
      };
      this.$http.post(api, user).then(response => {
        console.log(response.data);
      });
    },
    setfirebase() {
      var firebaseConfig = {
        apiKey: "AIzaSyDQ7wWBvdtWHIVT5wrE_MMfwf0R4Oca2lY",
        authDomain: "amycontact-ebd97.firebaseapp.com",
        databaseURL: "https://amycontact-ebd97.firebaseio.com",
        projectId: "amycontact-ebd97",
        storageBucket: "amycontact-ebd97.appspot.com",
        messagingSenderId: "983987932981",
        appId: "1:983987932981:web:1f019645b1daf95523467c",
        measurementId: "G-BTQYVNPJY6"
      };
      // Initialize Firebase
      firebase.initializeApp(firebaseConfig);
    },
    getContacts() {
      const vm = this;
      const db = firebase.firestore();
      const docRef = db.collection("myC");
      docRef.onSnapshot(querySnapshot => {
        vm.contactData = [];
        querySnapshot.forEach(function(doc) {
          vm.docId = doc.id;
          docRef.doc(vm.docId).update({
            Id: vm.docId,
          })
          vm.contactData.push(doc.data());
          console.log('123')
        });
      });
    },
    addContact() {
      const vm = this;
      const db = firebase.firestore();
      const docRef = db.collection("myC");
      docRef.add(vm.contact);
      vm.contact = {};
      // vm.getContacts();
    },
    delContacts(id) {
      const vm = this;
      const db = firebase.firestore();
      const docRef = db.collection("myC");
      docRef.doc(`${id}`).delete().then(() => {
        // vm.getContacts();
      });
    },
    getedit(item){
      this.editContact = item;
      this.editName = this.editContact.name;
      this.editPhone = this.editContact.phone;
      this.editAddress = this.editContact.address;
    },
    escedit(){
      this.editContact = {};
    },
    doneedit(item){
      const vm = this;
      const db = firebase.firestore();
      const docRef = db.collection("myC");
      docRef.doc(`${item.Id}`).update({
        name: vm.editName,
        phone: vm.editPhone,
        address: vm.editAddress,
      }).then(() => {
        this.editContact = {};
      });
    },
    editbtn(item){
      const vm = this;
      if(vm.editContact != item){
        vm.getedit(item);
      }else{
        vm.doneedit(item)
      }
    },
    check() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}logout`;
      this.$http.post(api).then(response => {
        console.log(response.data);
      });
    }
  },
  computed: {
    searchData() {
      const vm = this;
      return vm.contactData.filter(
        item => item[vm.filter].indexOf(vm.search) >= 0
      );
    }
  },
  created() {
    this.setfirebase();
    this.getContacts();
    // this.signin();
  },
  mounted() {}
};
</script>